* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}

 
  form {
    min-height: 10vh;
    max-width: 650px;
    width: 100%;
    margin:  auto;
    padding: 45px 25px;
    display: inline-block;
    justify-content: center;
    background-size: cover;
    background-attachment: fixed;
    align-items: center;
    
  }
  
  label,
  input {
    display: block;
    width: 100%;
  }
  
  label {
    margin-bottom: 5px;
    height: 25px;
  }
  
  input {
    margin-bottom: 20px;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #777;
  }
  
  input.error {
    border-color: red;
  }
  
  .input-feedback {
    color: rgb(235, 54, 54);
    margin-top: -15px;
    font-size: 13px;
    margin-bottom: 20px;
  }
  
  button {
    
    padding: 5px 15px;
    
    background-color: rgb(70, 153, 179);
    color: white;
    border: 1px solid rgb(70, 153, 179);
    background-color: 250ms;
  }
  
  button:hover {
    cursor: pointer;
    background-color: white;
    color: rgb(70, 153, 179);
  }
  